.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.Bloque {
  width: 100vw;
  max-width: 640px;
  height: 100vh;
  margin: 0;
  padding-top: 2vh;
  padding-bottom: 2vh;
  padding-left: 2vw;
  padding-right: 2vw;
  background-color: crimson;
  display: block;
}

.Titulo {
  background-color: darkgrey;
  width: 80vw;
  max-width: 512px;
  height: 10vh;
  position: absolute;
  left: 10vw;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Times New Roman", Times, serif;
  font-weight: bold;
  font-size: 5vw;
}

.Menu {
  width: 80vw;
  max-width: 512px;
  position: absolute;
  top: 11vh;
  left: 10vw;
  overflow-x: scroll;
}

.Cuerpo {
  background-color: whitesmoke;
  width: 80vw;
  max-width: 512px;
  height: 84vh;
  position: absolute;
  left: 10vw;
  margin-top: 12vh;
  overflow-y: scroll;
}

.Boton {
  background-color: thistle;
  display: inline-block;
  width: 32.5vw;
  max-width: 208px;
  height: 25vh;
  margin-left: 5vw;
  margin-top: 2.25vh;
}

.Barra {
  background-color: rgb(105, 99, 105);
  height: 7vh;
  position: absolute;
  left: 10vw;
  bottom: 2vh;
  display: flex;
  justify-content: center;
}

.Ancho {
  width: 80vw;
  max-width: 512px;
}

.Botoncito {
  width: 16vw;
  max-width: 102.4px;
  flex: 1;
  color: rgba(255, 255, 255, 0.7);
  padding: 6px 12px 8px;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    padding-top 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

@media screen and (min-width: 640px) {
  .Bloque {
    padding-left: 12.8px;
    background-color: teal;
  }
  .Titulo {
    position: absolute;
    left: 64px;
  }
  .Menu {
    position: absolute;
    left: 64px;
  }
  .Cuerpo {
    position: absolute;
    left: 64px;
  }
  .Barra {
    position: absolute;
    left: 64px;
  }
  .Boton {
    margin-left: 32px;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.pegado {
  display: flex;
  flex-wrap: wrap;
}

.izq {
  margin-right: 5px;
}
.shrink {
  display: flex;
  height: auto;
  font-size: 1.7vw;
}
.App-link {
  color: #61dafb;
}
.scrolling-wrapper {
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;

  .card {
    display: inline-block;
  }
}
.vscrolling-wrapper {
  overflow-x: hidden;
  overflow-y: scroll;
  white-space: nowrap;

  .card {
    display: inline-block;
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
